body{margin:0;padding:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;height:100vh}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.VolunteerRegister{display:flex;flex-direction:column;justify-content:center}.VolunteerRegister .header{display:flex;flex-direction:row;justify-content:space-around}.VolunteerRegister .container{display:flex;width:100%;justify-content:center}

.FieldForm{display:flex;flex-direction:column;justify-content:left;margin:10px 0}

.Label{margin-bottom:10px;font-size:1rem}

.InputFieldForm{width:calc(100% - 22px);height:30px;padding:10px 10px;font-size:1rem;border-radius:5px;border:1px solid lightgray}

.SubmitButton{min-height:40px;border-radius:5px;background:#EABD00;font-size:1rem;outline:none}.SubmitButton:hover{opacity:0.8}


.ContainerForm{display:flex;flex-direction:column;justify-content:space-around;align-content:center;width:100%}

.VolunteerLogin{display:flex;flex-direction:column;justify-content:center}.VolunteerLogin .header{display:flex;flex-direction:row;justify-content:space-around}.VolunteerLogin .container{display:flex;width:100%;justify-content:center}

.Home{display:flex;flex-direction:column;place-self:center}

.LinkButton{min-height:40px;max-width:400px;width:100%;margin-bottom:20px;border-radius:5px;background:#EABD00;font-size:1rem;padding:20px 30px;outline:none}.LinkButton:hover{opacity:0.8}

.WrapperPages{display:grid;grid-template-rows:100px auto 100px;height:100vh}

